
.demo-block {
  margin-bottom: 15px;
  border: 1px solid #e9e9e9;
  border-radius: 4px;
}
.demo-block p {
    margin-bottom: 0;
}
.demo-block code {
    font-size: 14px;
}
.demo-block .preview {
    padding: 30px 15px;
    border-bottom: 1px solid #e9e9e9;
}
.demo-block .description {
    position: relative;
    padding: 18px 30px 18px 15px;
}
.demo-block .description .icon-toggle-code {
      position: absolute;
      right: 12px;
      bottom: 18px;
      width: 16px;
      cursor: pointer;
}
.demo-block .description .icon-toggle-code:hover path {
        fill: #666;
}
.demo-block .source {
    position: relative;
    padding: 15px;
    background-color: #f7f7f9;
}
.demo-block .source .btn-clipboard {
      position: absolute;
      top: .5rem;
      right: .5rem;
      z-index: 10;
      display: block;
      padding: .25rem .5rem;
      font-size: 75%;
      color: #818a91;
      cursor: pointer;
      background-color: transparent;
      border: 0;
      border-radius: .25rem;
}
.demo-block .source .btn-clipboard:hover {
        color: #fff;
        background-color: #027de7;
}
.demo-block.opened .description {
    border-bottom: 1px solid #e9e9e9;
}



